export class AffordableCareReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			eligible_time_contributing_pay_code_array: null
		} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'AffordableCareReport';
		this.viewId = 'AffordableCareReport';
		this.context_menu_name = $.i18n._( 'Affordable Care' );
		this.navigation_label = $.i18n._( 'Saved Report' );
		this.view_file = 'AffordableCareReportView.html';
		this.api = TTAPI.APIAffordableCareReport;
		this.include_form_setup = true;
	}

	initOptions( callBack ) {
		var $this = this;
		var options = [
			{ option_name: 'page_orientation' },
			{ option_name: 'font_size' },
			{ option_name: 'chart_display_mode' },
			{ option_name: 'chart_type' },
			{ option_name: 'templates' },
			{ option_name: 'setup_fields' },
			{ option_name: 'eligible_time_contributing_pay_code' },
			{ option_name: 'auto_refresh' }
		];

		this.initDropDownOptions( options, function( result ) {

			callBack( result ); // First to initialize drop down options, and then to initialize edit view UI.

		} );
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			groups: {
				form: {
					label: $.i18n._( 'Form' ),
					id: this.viewId + 'Form'
				}
			},
			exclude: [],
			include: [
				{
					label: $.i18n._( 'Form' ),
					id: 'view_print',
					action_group_header: true,
					action_group: 'view_form',
					group: 'form',
					menu_align: 'right',
					icon: 'view-35x35.png',
					type: 2
				},
				{
					label: $.i18n._( 'Save Setup' ),
					id: 'save_setup',
					action_group: 'view_form',
					group: 'form',
					menu_align: 'right',
					}
			]
		};

		return context_menu_model;
	}

	buildFormSetupUI() {

		var $this = this;

		var tab3 = this.edit_view_tab.find( '#tab_form_setup' );

		var tab3_column1 = tab3.find( '.first-column' );

		this.edit_view_tabs[3] = [];

		this.edit_view_tabs[3].push( tab3_column1 );

		//Eligible Time Contributing Pay Code
		var form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input = form_item_input.AComboBox( {
			field: 'eligible_time_contributing_pay_code',
			set_empty: true,
			allow_multiple_selection: false,
			layout_name: 'global_option_column',
			key: 'value'
		} );

		form_item_input.setSourceData( $this.eligible_time_contributing_pay_code_array );
		this.addEditFieldToColumn( $.i18n._( 'Eligible Time Contributing Pay Code Policy' ), form_item_input, tab3_column1, 'first_last' );
	}

	getFormSetupData() {
		var other = {};
		other.eligible_time_contributing_pay_code = this.current_edit_record.eligible_time_contributing_pay_code;

		return other;
	}

	setFormSetupData( res_Data ) {

		if ( !res_Data ) {
			this.show_empty_message = true;
		}

		if ( res_Data ) {

			if ( res_Data.eligible_time_contributing_pay_code ) {
				this.edit_view_ui_dic.eligible_time_contributing_pay_code.setValue( res_Data.eligible_time_contributing_pay_code );

				this.current_edit_record.eligible_time_contributing_pay_code = res_Data.eligible_time_contributing_pay_code;
			}

		}
	}

}